import {Component, OnInit} from '@angular/core';
import {Router} from "@angular/router";
import {MyAppService} from "../my-app.service";

@Component({
    selector: 'app-login-com',
    templateUrl: './login-com.component.html',
    styleUrls: ['./login-com.component.less']
})
export class LoginComComponent implements OnInit {


    constructor(
        private route: Router,
        private myAppService: MyAppService
    ) {
    }

    ngOnInit() {
        window.sessionStorage.clear();
    }

    loginSubmit = () => {
        console.log('login OK!');
        this.myAppService.getMenuList().subscribe(res => {
            this.myAppService.MENUS_LIST = res.result;
            this.myAppService.MENUS_LIST.map(item => item['active'] = false);
            window.sessionStorage.setItem('menus', JSON.stringify(res.result));
            this.route.navigate(['/index'])
        })
    }

    checkView = () => {
        this.route.navigate(['/checkUtils'])
    }

}
