import { Component, OnInit } from '@angular/core';
import {MyAppService} from "../../my-app.service";
import {Subscription} from "rxjs/index";

@Component({
  selector: 'app-breadcrumb',
  templateUrl: './breadcrumb.component.html',
  styleUrls: ['./breadcrumb.component.less']
})
export class BreadcrumbComponent implements OnInit {

    breadItems: any[] = JSON.parse(window.sessionStorage.getItem('breadcrumb'));
    getBreadcrumbSubject: Subscription = new Subscription();
    _isShow: boolean = false;

  constructor(
      private appService: MyAppService
  ) { }

  ngOnInit() {
      console.log('breadcrumb init !')
      this.breadItems = this.appService.breadcrumbNameList;
      this._isShow = this.checkShow(this.breadItems);
      this.appService.changeBreadcrumb();
      this.getBreadcrumbSubject = this.appService.BreadcrumbSubject.subscribe(res => {
          this.breadItems = res;
          this._isShow = this.checkShow(res);
      });
  }

  checkShow = (data): boolean => {
      let flag = true
      data.map(item => {
          if (!item) flag = false;
      });
      return flag
  }


}
