import {Injectable} from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import {Observable, Subject} from 'rxjs';
import {ApiModule} from "../api/api";

@Injectable()
export class MyAppService {

    public MENUS_LIST: any[] = [];
    public MENUS_ACTIVE_ITEM: any = null;
    public breadcrumbNameList: any[] = [null, null, null];
    public breadcrumbNameListRES: any[] = [null, null, null];

    constructor(
        private $http: HttpClient,
        private HOST: ApiModule
    ) {
    }
    // 登录模拟获取菜单list
    getMenuList = (): any => {
        return this.$http
            .get('./assets/list.json')
    }

    // 激活二级菜单订阅
    public ActiveMenusSubject = new Subject<any>();
    changeActiveMenus = menu => {
        this.ActiveMenusSubject.next(menu);
    }

    // 面包屑订阅
    public BreadcrumbSubject = new Subject<any>();
    changeBreadcrumb = () => {
        this.breadcrumbNameList = [...this.breadcrumbNameListRES];
        this.BreadcrumbSubject.next(this.breadcrumbNameList);
    }
}
