import {Component, OnInit} from '@angular/core';
import {MyAppService} from "../../my-app.service";

@Component({
    selector: 'app-header',
    templateUrl: './header.component.html',
    styleUrls: ['./header.component.less']
})
export class HeaderComponent implements OnInit {
    HEADER_LIST: any[] = this.appService.MENUS_LIST; // 一级菜单
    headerURL: string = window.sessionStorage.getItem('headerURL'); // 所选菜单缓存

    constructor(
        private appService: MyAppService
    ) {
    }

    ngOnInit() {
        // 当登录状态刷新时,查询缓存
        if (this.HEADER_LIST.length < 1) this.HEADER_LIST = JSON.parse(window.sessionStorage.getItem('menus'));
        if (this.headerURL) {
            let activeItem = JSON.parse(this.headerURL);
            this.activeMenu(activeItem)
        } else {
            // 默认选中第一个
            this.HEADER_LIST[0].active = true;
            this.activeMenu(this.HEADER_LIST[0])
        }
    }

    // 激活二级菜单
    activeMenu = menus => {
        // this.appService.activeMenus = menus
        // this.HEADER_LIST.map(item => item.active = false);
        menus.active = true;
        this.HEADER_LIST.map(item => {
            item.id === menus.id ? item.active = true :  item.active = false;
        });
        window.sessionStorage.setItem('menus', JSON.stringify(this.HEADER_LIST))
        window.sessionStorage.setItem('headerURL', JSON.stringify(menus));
        this.appService.MENUS_ACTIVE_ITEM = menus;
        this.appService.breadcrumbNameListRES.splice(0,1, menus.name);
        this.appService.changeActiveMenus(menus);
    }

}
