import {Component, OnDestroy, OnInit} from '@angular/core';
import {MyAppService} from "../../my-app.service";
import {Subscription} from "rxjs/index";
import {Router} from "@angular/router";

@Component({
    selector: 'app-menu',
    templateUrl: './menu.component.html',
    styleUrls: ['./menu.component.less']
})
export class MenuComponent implements OnInit, OnDestroy {

    menus: any[] = [];
    getMenusSubscription: Subscription = new Subscription();
    menuURL: string = window.sessionStorage.getItem('menuURL');

    constructor(
        private appService: MyAppService,
        private route: Router
    ) {}

    ngOnInit() {
        console.log('menu init!');
        this.menus = this.appService.MENUS_ACTIVE_ITEM.subMenus || []; // 优先获取缓存headerURL - 补充第一次刷新时订阅模式未加载
        this.getMenusList();
        this.checkActiveURL()
        // if (this.menuURL) {
        //     this.routerLink(this.menuURL)
        //     return
        // }
        // if (this.menus.length < 1) {
        //     this.route.navigate(['/index']);
        //     /*const menus = JSON.parse(window.sessionStorage.getItem('menus'));
        //     this.appService.changeActiveMenus(menus[0])*/
        // }
    }

    getMenusList = (): void => {
        this.getMenusSubscription = this.appService.ActiveMenusSubject.subscribe(res => {
            this.menus = [];
            this.menus = [...res.subMenus];
        });
    };

    checkActiveURL = () => {
        const menuURL = JSON.parse(window.sessionStorage.getItem('menuItem')) || '';
        if (!!menuURL) {
            this.menus.map(item => {
                item['active'] = false;
                item.subMenus.map(option => {
                    option['active'] = false;
                    if (option.id === menuURL.id) {
                        item['active'] = true;
                        option['active'] = true;
                        this.appService.breadcrumbNameListRES.splice(1, 1, item.name);
                        this.appService.breadcrumbNameListRES.splice(2, 1, option.name);
                    }
                })
            });
            this.appService.changeBreadcrumb();
            console.log(this.appService.breadcrumbNameListRES)
        } else {
            // this.menus[0]['active'] = true;
            // this.menus[0].subMenus[0]['active'] = true;
            // this.routerLink(this.menus[0].subMenus[0])
        }

    };

    activeBreadcrumb = menu => {
        console.log(menu);
        this.appService.breadcrumbNameListRES.splice(1, 1, menu.name);
    }

    routerLink = (menuItem): void => {
        this.appService.breadcrumbNameListRES.splice(2, 1, menuItem.name);
        window.sessionStorage.setItem('breadcrumb', JSON.stringify(this.appService.breadcrumbNameListRES));
        this.appService.changeBreadcrumb();
        window.sessionStorage.setItem('menuItem', JSON.stringify(menuItem));
        this.route.navigate([menuItem.link]);
    }


    ngOnDestroy() {
        // this.getMenusSubscription.unsubscribe();
        window.sessionStorage.removeItem('menuItem')
    }

}
