import { Injectable } from '@angular/core';
@Injectable()

export class Utils {

	/**
	 * 判断整数(阈值9007199254740990以下)
	 * @param num - 待验证数据(number)
	 * @return Boolean
	 */
	_isInteger = num => {
		return Number.isInteger(num);
	}


	/**
	 * 判断正整数
	 * @param num - 待验证数据(number)
	 * @return Boolean
	 */
	_isPositiveInteger = num => {
		if (this._isInteger(num) && num > 0) {
			return true
		}
		return false;
	}


	/**
	 * 判断数组
	 * @param data - 待验证数据(array)
	 * @return - Boolean
	 */
	_isArray = data => {
		if (typeof Array.isArray === "function") {
			return Array.isArray(data);
		}else{
			return Object.prototype.toString.call(data) === "[object Array]";
		}
	}
	
	/**
	 * 分割数组
	 * @param data - 待验证数据(array)
	 * @param size - 分割长度(number)
	 * @return result - 新数组
	 */
	 _chunk = (data: any[], size): any[] => {
		const result = [];
		if (!this._isArray(data)) {
			console.error(`${data}非数组!`);
			return result
		}
		if (!this._isPositiveInteger(parseInt(size))) {
			console.error(`${size}非正整数`);
			return result
		}
		
		for (let i = 0; i < data.length; i += size) {
			result.push(data.slice(i, i + size))
		}

		return result
	}

	/**
	 * 删除数组中所有falsey值。falsey - false/null/0/""/undefined/NaN
	 * @param data - 待验证数据(数组)
	 */
	_compact = (data: any[]): any[] => {
		let resIndex = 0;
		const result = [];
		if (this._isArray(data)) {
			console.error(`${data}非数组!`);
			return result
		}
		if (data === null) {
			return result
		}
		for (const value  of data) {
			if (value) {
				result[resIndex++] = value
			}
		}
		return result
	}

	/**
	 * 合并数组
     * @param flag - 是否解析多层数组(boolean)
	 * @param data - 待验证数据(array)
	 * @return result - 新数组
	 */
	_concat = (flag: boolean, ...data: any[]): any[] => {
		let result = [];
		const arr = [...data];

        const analysisData = data => {
            if (this._isArray(data)) {
                data.map(item => {
                    if (this._isArray(item)) return analysisData(item);
                    result.push(item);
                });
            } else {
                result.push(data);
            }
        };

		if (flag) {
		    for (const item of arr) {
                analysisData(item);
            }
        } else {
            for (const item of arr) {
                this._isArray(item) ? result.push(...item) : result.push(item)
            }
        }
        console.log(result);
        return result;
	}

	/**
	 * 数组倒叙
	 * @param data - 待处理数组(array)
	 * @return result - 新数组
	 */
	_reverse = (data: any[] = []) => {
		let result = this._isArray(data) ? [...data] : null;
		// console.log(result);
		return result === null ? [] : result.reverse();
	}


	/**
	 * 排除数组中的值
	 * @param data - 待处理数组(array)
	 * @param values - 清除的值(...array)
	 * @return result - 新数组
	 */
	_difference = (data: any[], values: any[]): any[] => {
		let items = [...values];
		let result = [...data];

		items.map(item => {
			let index = data.findIndex(i => i === item);
			if (index > -1) {
				result.splice(index, 1);
			}
		})

		console.log(result)
		console.log(data)
		return result
	}

	// =========================================================================================
	_differenceBy = () => {}
	_differenceWith = () => {}
	// =========================================================================================
	
	/**
	 * 自0删除数组个数
	 * @param data - 待处理数组(array)
	 * @param num - 删除个数(number)
	 * @return result - 新数组
	 */
	_drop = (data: any[] = [], num: number = 1) => {
		let result = this._isArray(data) ? [...data] : [];
		result.length < 1 || num > result.length ? result = [] : result.splice(0, num);

		return result
	}

	/**
	 * 自-1删除数组个数
	 * @param data - 待处理数组(array)
	 * @param num - 删除个数(number)
	 * @return result - 新数组
	 */
	_dropRight = (data: any[], num: number = 1) => {
		let result = this._isArray(data) ? [...data] : [];
		result.length < 1 || num > result.length ? result = [] : result.splice(-num, num);

		return result
	}

	// ===========================================================================================
	_dropRightWhile = () => {}
	// ===========================================================================================

	/**
	 * 自动填充数组
	 * @param data - 待处理数组(array)
	 * @param value - 填充内容(any)
	 * @param startNum - 开始下标(number)
	 * @param endNum - 结束下标(number)
	 * @return result - 新数组
	 */
	_fill = (data: any[], value, startNum: number = 0, endNum: number = data.length): any[] => {
		let result = [...data];
		result.fill(value, startNum, endNum);
		return result
	}

	/**
	 * 查询数组内容
	 * @param data - 待处理数组(array)
	 * @param predicate - 筛选条件(function)
	 * @param startIndex - 查询起始下标(number) ---待补充
	 * @return index - 下标
	 */
	_findIndex = (data: any[], predicate?, startIndex?: number) => {
		let result = [...data];
		let index: number;
		if (!startIndex) startIndex = 0;
		index = result.findIndex(predicate)

		console.log(index)
		return index
	}

	// ===========================================================================================
	_findLastIndex = () => {}
	// ===========================================================================================

	/**
	 * 返回由键值组成的对象
	 * @param data - 待处理数组(array)
	 * @return resultObject - 新对象
	 */
	_fromPairs = (data: any[]) => {
		let result = [...data];
		let index: number = -1;
		const resultObject = {}; 
		while(++index < result.length) {
			let record = result[index];
			resultObject[record[0]] = record[1]; //将第一个作为键，第二个作为值添加到结果对象中
		}
		console.log(resultObject);
		return resultObject
	}

	/**
	 * 获取Array的第一个元素
	 * @param data - 待处理数组(array)
	 * @return element - 第一元素内容
	 */
	_head = (data: any[], index: number = 0) => {
		let result = [...data];
		let element = result[index] || undefined;
		console.log(element)
		return element
	}

	/**
	 * 获取查询内容在数组第一次出现的下标,负数从后向前查询
	 * @param data - 待查询数组(array)
	 * @param element - 查询内容(any)
	 * @param formNumber - 自n下标开始查询(负数为自后向前)
	 */
	_indexOf = (data: any[], element: any, formNumber: number = 0) => {
		// _reverse
		let index: number = formNumber;
		const len = data.length;
		while(++index < len) {
			if (data[index] === element) {
				return index;
			}
		}
		return -1;
	}


	/**
	 * 提取多个数组中的公共值
	 * @param data - 比对数组(arrays)
	 * @return result - 新数组
	 */
	_intersection = (...data): any[] => {
		// console.log(data);
		let copyData = [...data];
		let len: number; // 记录最小长度
		let _index: number; // 记录下标
		let minArr: any[] = []; // 记录最小长度数组
		let result: any[]; // 返回值

        copyData.map((item, i) => {
            if (!this._isArray(item)) return console.error(`${item},非数组！`);
            if (item.length === 0) return;
            if (len > item.length || !len) {
                len = item.lengt;
                minArr = item;
                _index = i;
            }
        });
        copyData.splice(_index, 1);

        if (minArr.length === 0) return result = [];

        result = Array.from(new Set(minArr.filter(item => new Set(...copyData).has(item))));

		// let result = Array.from(new Set(dataA.filter(v => new Set(dataB).has(v))));
		// result = result.flat(1); // flat测试方法,非稳定
		console.log(result);
		return result
	}

    // ===========================================================================================
    _intersectionBy = () => {}
    _intersectionWith = () => {}
    // ===========================================================================================

    /**
     * 将所有元素转换array为由\n分隔的字符串
     * @param data - 待处理数组(array)
     * @param separator - 分隔符(string)
     * @return result - 字符串
     */
    _join = (data: any[], separator: string = ',') => {
	    console.log(data, separator);
        let result = this._isArray(data) ? data.join(separator) : console.error(`${data}非数组！`);
        console.log(result)
        return result
    }

    /**
     * 将数组中最后元素返回
     * @param data - 待处理数组(array)
     * @return result - 最后一位元素
     */
    _last = (data: any[]): any => {
        let result = this._isArray(data) ? [...data].pop() : console.error(`${data}非数组！`);
        console.log(result)
        return result;
    }

    /**
     * 查询数组中待查询元素最后一次出现的位置
     * @param data - 待处理数组(array)
     * @param val - 查询元素(any)
     * @param len - 查询长度(number)
     * @return result - 最后一位元素
     */
    _lastIndexOf = (data: any[], val, len: number = data.length) => {
        console.time('first');
        let resultIndex = [...data].lastIndexOf(val, len);
        console.log(resultIndex);
        console.timeEnd('first');

        console.time('second');
        let result = [...data].reverse().indexOf(val, len);
        result = len === data.length ? [...data].length - result - 1 : result;
        console.log(result);
        console.timeEnd('second');
    }

    _nth = () => {}

}
